    margin-top: 20px;
    row-gap: 15px;
    grid-template-columns: repeat(2, 1fr);
  }
  .profile-box .item-icon {
    width: 20px;
    height: 20px;
  }
  .profile-box .img-inform span {
    font-size: 0.9rem;
  }
  .skill-box {
