    height: 150px;
  }

  .profile-box.projects-box {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10px;
    row-gap: 10px;
  }
  .project-box .inform-box{
    row-gap: 2px;
  }
  .project-box {
    width: 100%;
    height: calc((100vw - 140px - 15px) / 2);
  }
  .project-box .inform-box .position-text{
    white-space: normal;
  }
  .project-box .inform-box span {
      font-size: 0.8rem;

  }
  .project-box .title-btn span {
    font-size: 1rem;
  }
  button {
    padding: 2px 8px;
    border-radius: 15px;
  }

  .contact-box {
    flex-direction: column;
    row-gap: 40px;
    padding: 20px;
  }
  .contact-box > div {
    width: 100%;
  }
 
  .project-box .btn-box{
    column-gap: 5px;
  }
  
  footer .center,
  footer {
    height: 70px;
  }

}
