header{
    width: 100vw;
    height: 80px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99 !important;
    background-color: #fff;
}
header .line{
height: 4px;
position: absolute;
left: 0;
bottom: 0;
border-radius: 0 2px 2px 0;
background-color: #F2B3DC;
}

.logo{
width: 80px;
height: 80px;
}
header .center{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
header ul{
    display: flex;
    align-items: center;
    column-gap: 25px;
}